import React, { Component } from 'react';

import ReactDOM from 'react-dom';
import './index.css';
//import App from './App';
import registerServiceWorker from './registerServiceWorker';

let loginLoads = 0;
let painLevel;

const userLogin = (event) => {
    event.preventDefault();
    let user = event.target.elements.username.value;
    let passw = event.target.elements.password.value;
    //login would obviously not be done like this in reality
    if (user === 'patient' && passw === 'password') {
        renderPatient();
    } else {
        event.target.elements.username.value = '';
        event.target.elements.password.value = '';
        loginLoads++;
        renderLogin();
    }
};

class Notifications extends Component {
    render() {
        return (
            <p>You have no new notifications.</p>
        );
    }
};

class Links extends Component {
    render() {
        return (
            <div>
                <LinkButton title="Check-up questionnaire" target={renderCheckup} />
                <LinkButton title="Check profile" target={renderProfile} />
                <LinkButton title="Latest report" target={renderReport} />
                <LinkButton title="Contact" target={renderContact} />
            </div>
        );
    }
};

class LinkButton extends Component {
    render() {
        return (
            <div className="col-md-3">
                <div className="bgbox clickable" onClick={this.props.target}>
                    <h3>{this.props.title}</h3>
                </div>
            </div>
        );
    }
};

class Header extends Component {
    render() {
        return (
            <div className="jumbotron">
                <h1>{this.props.title}</h1>
            </div>
        )
    }
};

class PatientPage extends Component {
    render() {
        return (
            <div>
                <Header title="Welcome, Irmeli!" />
                <Notifications />
                <Links />
                <p className="clickable" onClick={renderLogin}>Log out</p>
            </div>
        )
    }
};

const getErrors = () => {
    if (loginLoads > 0) {
        return <p><strong>Error:</strong> Username and/or password was incorrect, please try again!</p>;
    }
};

class LoginForm extends Component {
    render() {
        return (
            <div>
                <form onSubmit={userLogin}>
                    <p>Please check in</p>
                    <input type="text" name="username" placeholder="username"></input>
                    <input type="text" name="password" placeholder="password"></input>
                    <button type="submit">Log in</button>
                </form>
            </div>
        );
    }
};

const Errors = props => <p>{getErrors()}</p>;

const LoginPage = props => (
    <div>
        <Header title="Welcome to MedCheck!" />
        <LoginForm />
        <Errors />
    </div>
);

class Section extends Component {
    render() {
        return (
            <div className="questionbox">
                <h4>{this.props.text}</h4>
                {this.props.subtext}
            </div>
        );
    }
};

const handlePainLevel = (event) => {
    event.preventDefault();
    const radios = document.getElementsByName('pain');

    for (let i = 0; i < radios.length; i++) {
        if (radios[i].checked) {
            painLevel = radios[i].value;
        }
    }
    console.log('pain level is ', painLevel);
    renderCheckup();
};

const PainLevelRadios = props => (
    <form onSubmit={handlePainLevel}>
        <PainLevel level="0" />
        <PainLevel level="1" />
        <PainLevel level="2" />
        <PainLevel level="3" />
        <PainLevel level="4" />
        <PainLevel level="5" />
        <PainLevel level="6" />
        <PainLevel level="7" />
        <PainLevel level="8" />
        <PainLevel level="9" />
        <PainLevel level="10" />
        <button type="submit">Select</button>
    </form>
);

class PainLevel extends Component {
    render() {
        return (
            <label className="radio-inline"><input type="radio" name="pain" value={this.props.level}/>{this.props.level}</label>
        );
    }
};

const Questions = props => (
    <div>
        <div id="question1">
            <Section text="What is your current pain level?" subtext={<PainLevelRadios />} />
            <PainExplanation />
            <PainLevelQuestions />
        </div>
    </div>
);

class PainExplanation extends Component {
    render() {
        if (painLevel === '0') {
            return <p>0: You have no pain at all.</p>
        } else if (painLevel === '1') {
            return <p>1: Your pain is hardly noticable.</p>
        } else if (painLevel === '2') {
            return <p>2: Your pain is mild and you are aware of your pain when you pay attention to it.</p>
        } else if (painLevel === '3') {
            return <p>3: Your pain is tolerable and you dont notice it most of the time.</p>
        } else if (painLevel === '4') {
            return <p>4: Your pain is moderate and you are aware of it most of the time but you can continue most daily activities.</p>
        } else if (painLevel === '5') {
            return <p>5: Your pain is strong and distracting and you are unable to perform some daily activities.</p>
        } else if (painLevel === '6') {
            return <p>6: Your pain is intense and can cause you to think unclearly at times. You think about the pain all of the time and are unable to do many daily activities.</p>
        } else if (painLevel === '7') {
            return <p>7: Your pain is unmanageable and keeps you from doing most of your daily activities.</p>
        } else if (painLevel === '8') {   
            return <p>8: Your pain is horrible and talking and listening are hard because of it.</p>
        } else if (painLevel === '9') {    
            return <p>9: Your pain is so severe that you cannot tolerate it and are unable to talk or move much.</p>
        } else if (painLevel === '10') {
            return <p>10: You are unable to move and will go unconscious soon because of unimaginable pain.</p>
        } else {
            return null;
        }
    }
};

const Textbox = props => (
    <form>
        <textarea name="sideEffects"/>
    </form>
);

class PainLevelQuestions extends Component {
    render() {
        if (painLevel < 4) {
            return (
                <div>
                    <Section text="Does the pain impair your ability to perform daily activities such as working, socializing etc.?" />
                    <Section text="How many pills have you taken per day?" />
                    <Section text="How satisfied are you with your medication?" />
                    <Section text="Have you noticed any side effects? If you have, please describe them." answer={Textbox} />
                    <Section text="Do the side effects impair your ability to perform daily activities such as working, socializing etc.?" />
                    <SubmitButton />
                </div> 
            );
        } else if (painLevel < 7) {
            return (
                <div>
                    <Section text="How do you cope with the pain?" />
                    <Section text="Does the pain impair your ability to sleep?" />
                    <Section text="How many pills have you taken per day?" /> 
                    <Section text="Have you experienced any severe side effects (such as bleeding)?" />    
                    <SubmitButton />                                   
                </div>
            );
        } else if (painLevel >= 7) {
            return <Section text="Notification: In case of severe acute pain, contact the emergency number or your doctor immediately." />;
        } else {
            return null;
        }
    }
};

const SubmitButton = props => (
    <button onClick={renderThankYou}>Submit</button>
);

const ThankYouPage = props => (
    <div>
        <Header title="Thank you!" />
        <p>Your answers have been submitted.</p>
        <BackToMain />
    </div>
);

const BackToMain = props => <p className="clickable" onClick={renderPatient}>Back to front page</p>;

const CheckupPage = props => (
    <div>
        <Header title="New checkup" />
        <Questions />
        <BackToMain />
    </div>
);

class Medication extends Component {
    render() {
        return (
            <div>
                <h5><strong>{this.props.name}</strong></h5>
                <p>Instructions: take {this.props.often} times a day.</p>
            </div>
        )
    }
};

const Medications = props => (
    <div>
        <Section text="Current prescriptions:" subtext={<div><Medication name="Tramadol 300mg" often="3"/><Medication name="Gabapentin 800mg" often="2" /></div>} />
    </div>
);

const ProfilePage = props => (
    <div>
        <Header title="Irmeli Korhonen" />
        <Medications />
        <BackToMain />
    </div>
);

const ReportPage = props => (
    <div>
    </div>
);

const ContactPage = props => (
    <div>
    </div>
);

const root = document.getElementById('root');

const renderPatient = () => ReactDOM.render(<PatientPage />, root);
const renderLogin = () => ReactDOM.render(<LoginPage />, root);
const renderCheckup = () => ReactDOM.render(<CheckupPage />, root);
const renderThankYou = () => ReactDOM.render(<ThankYouPage />, root);
const renderProfile = () => ReactDOM.render(<ProfilePage />, root);
const renderReport = () => ReactDOM.render(<ReportPage />, root);
const renderContact = () => ReactDOM.render(<ContactPage />, root);

//muuta takas loginiks
renderProfile();
registerServiceWorker();





